<template>
    <!--begin::Modal-->  
    <div class="modal fade" id="formModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel" >{{ form.id ? 'تحديث':'إضافة جديد' }}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body row">
                    <div class="col-md-6 mt-3">
                        <label >نوع المصروف</label>
                        <v-select v-if="types" dir="rtl" label="name" class="vselect" style="width: 100%;"
                                    :options="types" :reduce="val => val.id" v-model="form.type_id"></v-select> 
                        <has-error :form="form" field="type_id"></has-error>
                    </div> 
                    <div class="col-md-6 mt-3">
                        <label >المبلغ</label>
                        <input v-model="form.money" type="number" autocomplete="off"
                        class="form-control" :class="{ 'is-invalid': form.errors.has('money') }">
                        <has-error :form="form" field="money"></has-error>
                    </div> 
                    <div class="col-md-6 mt-3">
                        <label >البيان</label>
                        <input v-model="form.note" type="text" autocomplete="off"
                        class="form-control" :class="{ 'is-invalid': form.errors.has('note') }">
                        <has-error :form="form" field="note"></has-error>
                    </div> 
                    <div class="col-md-6 mt-3">
                        <label >تاريخ المصروف</label>
                        <Datepicker input-class="form-control" v-model="form.created_at" format="dd-MM-yyyy"></Datepicker>
                        <has-error :form="form" field="money"></has-error>
                    </div> 
                    <div class="col-md-12 mt-3">
                        <label >ملاحظات</label>
                        <textarea v-model="form.note1" rows="1" dir="rtl" cols="50" type="text" 
                            autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('note1') }"></textarea>
                        <has-error :form="form" field="note1"></has-error>
                    </div> 
                </div> 
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal" >غلق</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary" >تحديث</button>
                    <button v-else type="submit" class="btn btn-primary" >حفظ</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>

<script>
import {Form,HasError} from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";
import Datepicker from 'vuejs-datepicker';


export default {
    components: {HasError,Datepicker},
    props: ['fetchData'],
    data() {
        return {
            form: new Form({id:null,money:null,type_id:null,note:null,note1:null,created_at:new Date().toLocaleString()}),
            types:null
        };
    }, 
    methods: {
        getTypes(){
            axios.get('/api/payment_types',{params: {paginate:0}}).then(({data}) => {
                this.types=data.success.payment_types;
            }).catch((error) => {console.log("Error......")})
        },
        create(){
            this.getTypes();
            this.form.reset();
            $('#formModal').modal('show');
        },
        store(){ 
            this.form.post("/api/payments").then(({data}) => {
                if (data.message) {
                    this.$swal.fire({icon: 'error',title: 'Sorry',text: data.message+'!',
                        footer: '<a href>Why do I have this issue?</a>'
                    });
                } else {
                    this.fetchData();
                    $('#formModal').modal('hide');
                }
            }).catch((error) => {console.log("Error......")})
        },  
        edit(form){ 
            this.getTypes();
            this.form.fill(form);
            $('#formModal').modal('show');
        },
        update(){
            this.form.put("/api/payments/"+this.form.id).then((res) => {
                this.fetchData();
                $('#formModal').modal('hide');
            }).catch((error) => {console.log("Error......")})
        },  
        destroy(id) {
            // $('#formModal').modal('hide');
            this.$swal.fire({
                title:'تحذير',text:'هل أنت متأكد من الحذف',
                icon: 'warning',showCancelButton: true,confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',cancelButtonText: 'تراجع',
                confirmButtonText: 'نعم إحذف',
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/payments/'+id).then(response => {
                        if(response.data.message){
                            this.$swal.fire({icon: 'error',title: 'Oops...',text: response.data.message+'!'});
                        }else{
                            this.$swal.fire("تم","تم الحذف بنجاح",'success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({icon: 'error',title: 'Oops...',text: 'Something went wrong!'}); 
                    });
                }
            })
        },
    },
}
</script>
